<template>
    <div class="main">
    <div class="main__background__form">  
        <div class="main__background__form__logo" >
        <img :src="require('@/assets/LOGO.png')" alt="">
        </div> 
        <div class="main__background__form__headline">
            <h2>Digitální kancelář</h2>
            <h5>Šetří Váš čas i peníze</h5>
        </div>   
        <div class="main__background__form__loga">
         <img :src="require('@/assets/gdpr-ready-badge-text.png')" alt="">  
         <img :src="require('@/assets/eidas-ready-logo.png')" alt=""> 
         <img :src="require('@/assets/qes_2014_stamp_iso9001_gr_poz.png')" alt="">
         <img :src="require('@/assets/qes_2014_stamp_iso27001_gr_poz.png')" alt="">
         <p>Copyright © NUBEO Technologies, s.r.o.</p>   
        </div>
    </div>
    <div v-if="!loginFailed && !loginSuccess && isLoginForm"  @submit.prevent="login"  class="main__form">
        <div class="main__form__frame69">
            <div class="main__form__frame69__frame66">
            <h1>Přihlášení</h1>
            <p>Jste nový uživatel?  <a @click="registerForm"  href="#"> Registrovat se</a></p>
            </div>
            <div class="main__form__frame69__frame68"> 
                <div class="main__form__frame69__frame68__frame67">
                    <div :class="{'main__form__frame69__frame68__frame67__email__input' : !invalidLogin, 'main__form__frame69__frame68__frame67__email__input__err' : invalidLogin}">
                    <p>Email</p>
                    <label> 
                        <input 
                        :class="{ error: invalidLogin}"
                        id="email" 
                        :type="email"
                        v-model="email"
                        >
                    </label>
                    </div>
                    <div :class="{'main__form__frame69__frame68__frame67__password__input' : !invalidPassword, 'main__form__frame69__frame68__frame67__password__input__err' : invalidPassword}">
                        <p>Heslo <a href="#">Zapomněli jste heslo?</a> </p>                        
                        <label>
                            <input
                            :type="show === true ? 'text' : 'password'"
                            :class="{ error: invalidPassword}"
                            id="password"
                            v-model="password"
                            >
                            <span
                             class="main__form__frame69__frame68__frame67__password__input__show-icon"
                             v-if="show"
                             @click="show = !show"
                             >
                            <svg width="17" height="9" viewBox="0 0 17 9" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M0.5 1C2.24471 4.66667 4.91225 6.5 8.50262 6.5C12.093 6.5 14.7588 4.66667 16.5 1M8.5 6.5V8.5M12.7929 5.79289L14.2071 7.20711M2.79289 7.20711L4.20711 5.79289" stroke="#888888" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>
                        </span> 
                             <span 
                            class="main__form__frame69__frame68__frame67__password__input__hide-icon"
                            v-else 
                            @click="show = !show"
                            >
                                <svg width="18" height="13" viewBox="0 0 18 13" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" clip-rule="evenodd" d="M8.99712 9.486C7.34361 9.486 6.00809 8.147 6.00809 6.4955C6.00809 4.853 7.34311 3.5145 8.99712 3.5145C10.6511 3.5145 11.9901 4.853 11.9901 6.4955C11.9901 8.147 10.6506 9.486 8.99712 9.486ZM9.00262 1C4.90008 1 2.30306 4.052 1.24655 5.6065C0.923552 6.086 0.916052 6.917 1.23505 7.3975C2.26756 8.961 4.81758 12 9.00262 12C13.1827 12 15.7332 8.961 16.7652 7.3975C17.0847 6.917 17.0757 6.086 16.7532 5.6065C15.6967 4.052 13.1047 1 9.00262 1Z" stroke="#888888" stroke-linecap="round" stroke-linejoin="round"/>
                                </svg>
                            </span>
                        </label> 
                          
                    </div>
                    <div class="main__form__frame69__frame68__checkbox">
                        <input class="main__form__frame69__frame68__checkbox__custome" v-model="isChecked"  @change="toggleCheckbox" type="checkbox" > 
                         <span class="main__form__frame69__frame68__checkbox__custome__icon"></span>
                         Zapamatovat přihlášení
                          
                    </div>
                </div>
                <div class="main__form__frame69__frame68__button">
                    <button
                    @click="login"
                    type="submit"
                    >Přihlásit se</button>
                </div>
                <div class="main__form__frame69__frame68__login-status">
                <p v-if="invalidLogin || invalidPassword"
                class="main__form__frame69__frame68__login-status__item"
                > <svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M1.23828 1.40186L10.7617 10.5979M10.7617 1.40186L1.23828 10.5979L10.7617 1.40186Z" stroke="#E4162F" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg> 
                    Zadali jste nesprávné přihlašovací údaje.
                
                </p>
            </div>
            </div>
            </div> 
            <div class="main__form__items">
                <div class="main__form__items__gdpr">
                    <a href="#">Prohlášení o GDPR </a> |
                    <a href="#">Veřejné obchodní podmínky</a>

                </div>
                <div class="main__form__items__system">
                    <div class="main__form__items__system__elips"></div>
                    Všechny systémy v provozu
                </div>  
            </div>
        
    </div>
        
    <div v-if="!isLoginForm" class="main__register">
        <div v-if="mailInput === true" class="main__register__frame58">
            <div class="main__register__frame58__frame57">
                <h2>
                    Registrace
                </h2>
                <span>Máte účet? 
                <a @click="registerForm" href="#">
                    Přihlásit se
                </a>
            </span>
            </div>
            <p>
                Na e-mail Vám bude zaslán ověřovací kód. Po ověření  prosím pokračujte v dalších krocích registrace.
            </p>
        </div>
        <div v-if="mailInput === true" class="main__register__email">
            <label for="email"> E-mail
                <input 
                        id="email" 
                        :type="email"
                        v-model="email"
                        >
            </label>
                    <button @click="registerVerify" :class="{'main__register__email__button' : email.length === 0}">Pokračovat</button>
        </div>
        <div v-if="mailVerify === true"  class="main__register__verify">
            <div class="main__register__verify__form">
                <div class="main__register__verify__form__frame57">
                    <h2>
                        Zadejte kód
                    </h2>
                    <a @click="registerForm" href="#">Zpět na přihlášení</a>
                </div>
                <div class="main__register__verify__form__text">
                    Potřebujeme ověřit vaši identitu, zadejte prosím ověřovací PIN kód, který jsme vám poslali na e-mail {{ email }}
                </div>
                <div class="main__register__verify__form__code-input">
                    <div @input="limit" :class="{'main__register__verify__form__code-input__items' :!invalidCode, 'main__register__verify__form__code-input__items__err' :invalidCode } ">
                     <input id="code"  v-model="inputValue1" @input="limit" :type="code">
                     <input id="code"  v-model="inputValue2" @input="limit"  :type="code">
                     <input id="code"  v-model="inputValue3" @input="limit"  :type="code">
                     <input id="code"  v-model="inputValue4" @input="limit"  :type="code">
                    </div>
                     <p :class="{'main__register__verify__form__code-input' :!invalidCode, 'main__register__verify__form__code-input__err' :invalidCode}">
                        Nesprávný ověřovací kód. Prosím zkontrolujte jeho správnost nebo požádejte o nový.
                    </p>
                     <button @click="passwordForm">Ověřit a pokračovat</button>
                 </div>
                 <div class="main__register__verify__form__spam">
                    <p>Nepřišel Vám e-mail? Prosím zkontrolujte spam.</p>
                    <a href="">Zaslat znovu kód</a>
                 </div>
            </div>
        </div>
        <div class="main__register__password">
            <div class="main__register__password__frame58">
                <div class="main__register__password__frame58__frame57">
                    <h2>
                        Tvorba hesla
                    </h2>
                    <a href="#">
                        Zpět na přihlášení
                    </a>
                </div>
                <div class="main__register__password__frame58__text">
                    Děkujeme za vaše ověření. Nyní si můžete nastavit heslo. Dbejte prosím,
                    aby heslo bylo dostatečně silné a neobsahovalo vaše iniciály či jiné snadno uhodnutelné slova.
                </div>
            </div>
            <div class="main__register__password__input">
                <label for="password">
                    <input type="password">
                     <ul>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                        <li></li>
                     </ul>
                </label>
            </div>
        </div>
    </div>

    <div v-if="loginSuccess === true" class="main__from__login-success">
            <img class="main__from__login-success__img" :src="require('@/assets/happyrobotassistantwavinghello.png')" alt="#">
            <div class="main__from__login-success__mail">
                <h2>Přihlášení proběhlo úspěšně</h2>
                <h5>Prosím vyčkejte na přesměrování....</h5>
            </div>
        </div> 
        <div v-if="loginFailed === true" class="main__from__login-unsuccess">
            <img class="main__from__login-unsuccess__img" :src="require('@/assets/robotassistantsad.png')" alt="#">
            <div class="main__from__login-unsuccess__mail">
                <h2>Přihlášení se nezdařilo</h2>
                <h5>Přihlášení neproběhlo z důvodu chyby na serveru. Prosím opakujte proces znovu nebo nás kontaktujte na e-mail podpora@esigno.com..</h5>
                <button
                @click="resetForm"> Zkusím to znovu </button>
            </div>
        </div>
    </div>
    
</template>

<script>

export default {
  data: () => {
    return {
      email: '',
      password: '',
      show: false,
      loginFailed: false,
      loginStatus: true,
      loginSuccess:false,
      isChecked: false,
      invalidLogin: false,
      invalidPassword: false,
      isLoginForm: true,
      mailInput: true,
      mailVerify: false,
      buttonDisable: true,
      inputValue1: '',
      inputValue2: '',
      inputValue3: '',
      inputValue4: '',


    };
  },
  methods:{
    login() {
        if(this.email === 'adc' && this.password === '123'){
            this.loginSuccess = true;
            this.loginFailed = false;
        } else {
            this.loginSuccess = false;
            this.loginFailed = true;
            
        }
        if(this.email !== 'adc'){
            this.invalidLogin = true;
        } else {
            this.invalidLogin = false;
        }
        if(this.password !=='123'){
            this.invalidPassword = true;
        } else {
            this.invalidPassword = false;
        }
    },
    resetForm() {
        this.loginFailed = false;
        this.loginSuccess = false;
    },
    registerForm(){
        this.isLoginForm = !this.isLoginForm;   
    },
    registerVerify(){
        if(this.email.length > 0){
             this.mailInput = false;
            this.mailVerify = true;
        }
    },
    limit(){
       if(this.inputValue1.length > 1){
        this.inputValue1 = this.inputValue1.charAt(0);    
        } 
        if(this.inputValue2.length > 1){
            this.inputValue2 = this.inputValue2.chatAt(0);
        }
        if(this.inputValue3.length > 1){
            this.inputValue3 = this.inputValue3.chatAt(0);
        }
        if(this.inputValue4.length > 1){
            this.inputValue4 = this.inputValue4.chatAt(0);
        }
    },

}
  
};

</script>

<style  lang="scss">
@import "@/style.scss";
</style>